import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import '../style/SummaryPage.css';

const SummaryPage = () => {
    const location = useLocation();
    const navigate = useNavigate();
    const { date, restaurant, movie } = location.state || {};
  
    return (
      <div className="summary-container">
        <h1 className="summary-title">Summary Page</h1>
        <div className="summary-card">
          <p className="summary-item">
            <strong>Date:</strong> {date ? new Date(date).toLocaleDateString() : 'No date selected'}
          </p>
          <p className="summary-item">
            <strong>Restaurant:</strong> {restaurant || 'No restaurant selected'}
          </p>
          <p className="summary-item">
            <strong>Movie:</strong> {movie || 'No movie selected'}
          </p>
        </div>
      </div>
    );
  };
  
export default SummaryPage;